"use client";

import React, { useState } from 'react';
import { Users, Search, Loader2, Download } from 'lucide-react';

export default function LeadsEnginePage() {
  const [loading, setLoading] = useState(false);
  const [industry, setIndustry] = useState('');
  const [location, setLocation] = useState('');
  const [results, setResults] = useState<any[]>([]);

  const handleSearch = () => {
    setLoading(true);
    setTimeout(() => {
      setResults([
        { name: "John Smith", role: "CEO", company: "TechFlow", email: "john@techflow.io" },
        { name: "Sarah Connor", role: "VP Sales", company: "SkyNet", email: "sarah@skynet.com" },
        { name: "Mike Ross", role: "Partner", company: "Pearson Specter", email: "mike@psl.com" },
      ]);
      setLoading(false);
    }, 1500);
  };

  return (
    <div className="min-h-screen bg-[#0B1120] text-white p-6 lg:p-8">
      <div className="max-w-[1600px] mx-auto grid grid-cols-1 gap-8">
        
        <div className="bg-[#1E293B] rounded-2xl border border-gray-800 p-6 shadow-xl">
          <div className="flex items-center gap-3 mb-6">
            <Users className="text-[#00C26D] h-6 w-6" />
            <h2 className="text-xl font-bold">Find Leads</h2>
          </div>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 items-end">
            <div className="space-y-2">
              <label className="text-sm text-gray-400 font-medium">Industry</label>
              <input type="text" value={industry} onChange={(e) => setIndustry(e.target.value)} className="w-full bg-[#0F172A] border border-gray-700 rounded-lg px-4 py-3 text-white focus:ring-1 focus:ring-[#00C26D] outline-none" placeholder="e.g. SaaS" />
            </div>
            <div className="space-y-2">
              <label className="text-sm text-gray-400 font-medium">Location</label>
              <input type="text" value={location} onChange={(e) => setLocation(e.target.value)} className="w-full bg-[#0F172A] border border-gray-700 rounded-lg px-4 py-3 text-white focus:ring-1 focus:ring-[#00C26D] outline-none" placeholder="e.g. London" />
            </div>
            <button onClick={handleSearch} disabled={loading} className="w-full bg-[#00C26D] hover:bg-[#00A05A] text-white font-bold py-3 rounded-lg transition-all flex items-center justify-center gap-2">{loading ? <Loader2 className="animate-spin h-5 w-5" /> : <><Search className="h-5 w-5"/> Search Database</>}</button>
          </div>
        </div>

        {results.length > 0 && (
          <div className="bg-[#1E293B] rounded-2xl border border-gray-800 p-6 shadow-xl">
             <div className="flex justify-between items-center mb-6">
                <h2 className="text-xl font-bold">Results</h2>
                <button className="text-[#00C26D] flex items-center gap-2 text-sm font-medium"><Download className="h-4 w-4"/> Export CSV</button>
             </div>
             <div className="overflow-x-auto">
                <table className="w-full text-left border-collapse">
                   <thead>
                      <tr className="border-b border-gray-700 text-gray-400 text-sm">
                         <th className="p-4">Name</th>
                         <th className="p-4">Role</th>
                         <th className="p-4">Company</th>
                         <th className="p-4">Email</th>
                      </tr>
                   </thead>
                   <tbody className="text-gray-300">
                      {results.map((lead, i) => (
                         <tr key={i} className="border-b border-gray-800 hover:bg-[#0F172A] transition-colors">
                            <td className="p-4 font-medium text-white">{lead.name}</td>
                            <td className="p-4">{lead.role}</td>
                            <td className="p-4">{lead.company}</td>
                            <td className="p-4 text-[#00C26D]">{lead.email}</td>
                         </tr>
                      ))}
                   </tbody>
                </table>
             </div>
          </div>
        )}

      </div>
    </div>
  );
}